.parent {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    /* padding-top: 10px;
    padding-left: 8%;
    padding-bottom: 10px; */
    background-color: #fefcf9;
    /* justify-items: center; */
    padding-top: 20px;
    margin-left: 2%;
    margin-right: 2%;

}

