.parent2 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    /* display: flex; */
    justify-items: center;
}

.divdoc1 {
    grid-area: 1 / 1 / 2 / 2;
    background-color: #E9B449;
    height: 130px;
    width: 100%;
    
    border-radius: 10px;
    display: flex;
    align-items: center;
}

.divdoc2 {
    grid-area: 1 / 2 / 2 / 3;
    background-color: #E9B449;
    height: 130px;
    width: screen;
