.parent3 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    justify-items: center;
    padding-right: 100px;
}

.divcontact1 {
    grid-area: 1 / 1 / 2 / 2;
    height: 450px;
    width: 300px;
    display: flex;
    background-color: #E9B449;
    /* height: 130px; */
    /* width: 300px; */
    border-radius: 10px;
    display: flex;
    justify-content: center;
    
}

